@import url(https://fonts.googleapis.com/css?family=Poppins:100,100italic,200,200italic,300,300italic,regular,italic,500,500italic,600,600italic,700,700italic,800,800italic,900,900italic);
* {
  margin: 0;
  padding: 0;
  font-family: "Poppins", sans-serif;
}

h1,
h2,
h3,
h4,
h5,
h6 {
  color: #212529
}

a {
  text-decoration: none;
}

p {
  line-height: 28px;
  color: #212529;
}


/* CSS responsive */

@media screen and (max-width: 567px) {
    html {
      font-size: 10px;
    }
}

@media (min-width:768px) {
  html {
    font-size: 12px;
  }
}

@media screen and (min-width: 1024px) {

    html {
      font-size: 14px;
    }
}
.card-video {
    display: flex;

}

.card-video img {
    width: 350px;
    height: 250px;
    border-radius: 10px;
}

.card-video:hover {
    background: #eeeeee;
    overflow: hidden;
    border-radius: 10px;
}

.card-video .author img {
    height: 35px;
    width: 35px;
}




/* CSS responsive */

@media screen and (max-width: 567px) {
    .card-video img {
        width: 150px;
        height: 100px;
        border-radius: 10px;
    }

    .card-video .author img {
        height: 15px;
        width: 15px;
    }
}

@media (min-width:768px) {
    .card-video img {
        width: 200px;
        height: 150px;
        border-radius: 10px;
    }

    .card-video .author img {
        height: 20px;
        width: 20px;
    }
}

@media screen and (min-width: 1024px) {

    .card-video img {
        width: 300px;
        height: 200px;
        border-radius: 10px;
    }

    .card-video .author img {
        height: 25px;
        width: 25px;
    }
}

.page-wrap {
    height: 100vh;
}

